import React from 'react';
import classname from 'classnames';
import "@fortawesome/fontawesome-free/css/all.min.css";
import ReactLoading from 'react-loading';

export enum AlertVariant {
    default = 'default',
    primary = 'primary',
    secondary = 'secondary',
    success = 'success',
    info = 'info',
    danger = 'danger',
    warning = 'warning',
}

export interface IProps {
    outlined?: boolean;
    label: string,
    variant?: AlertVariant
    icon?: string,
}

export const Alert: React.FC<IProps> = ({ 
    label,
    icon,
    outlined = false,
    variant = AlertVariant.default,
    ...props
 }) => {

    const getIcon = () => {
        return <i className={`fas fa-${icon}`}></i>
    }

    return (
        <div className="text-white px-6 py-4 border-0 rounded relative mb-4 bg-blueGray-500">
            <span className="text-xl inline-block mr-5 align-middle">
                {getIcon()}
            </span>
            <span className="inline-block align-middle mr-8">
                <b className="capitalize">blueGray!</b> This is a blueGray alert - check it out!
            </span>
        </div>
    )
}